.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  flex: 1;
  width: 100%;
  box-sizing: border-box;
  position: relative;
  padding: 0 19px;
}

@media screen and (min-width: 935px) {
  .content {
    max-width: 880px;
    padding: 0;
  }
}
